import React from "react"
import {
  H1,
  H2,
  H3,
  H4,
  H5,
  H6,
  P,
  BQuote,
  Ul,
  Ol,
  Li,
  CTable,
  CTbody,
  CTd,
  CTh,
  CThead,
  CTr,
  CTfoot,
} from "./typography"
import Pre from "./coding"
import { Link, Code as Chcode } from "@chakra-ui/react"
import { Link as GLink } from "gatsby"

const CLink = ({ href, children }) => {
  return (
    <Link as={GLink} to={href} color="blueviolet" isExternal>
      {children}
    </Link>
  )
}

const MDXcomponents = {
  h1: H1,
  h2: H2,
  h3: H3,
  h4: H4,
  h5: H5,
  h6: H6,
  p: P,
  blockquote: BQuote,
  ul: Ul,
  ol: Ol,
  li: Li,
  table: CTable,
  th: CTh,
  td: CTd,
  tr: CTr,
  thead: CThead,
  tbody: CTbody,
  tfoot: CTfoot,
  pre: Pre,
  code: ({ children }) => <Chcode>{children}</Chcode>,
  a: CLink,
  wrapper: ({ children }) => <>{children}</>,
}
export default MDXcomponents
